<style lang="less">
  /*@import '../../styles/common.less';*/
  /*@import 'components/table.less';*/
</style>

<template>
    <div >
        <Card>
              <Form ref="formItem" class="" :model="formInline" label-position="right" label-width="50" style="height: 30px; "  >
                    <Row>
                                      <div style="float: left;margin-top: 5px;font-size: 14px;display: inline-block">
                                         <Icon type="gear-b" ></Icon>
                                        <strong> </strong>
                                      </div>

                                    <Col span="6">
                                    <FormItem label=":" prop="computer_room" >
                                      <Input v-model="redis_host" placeholder="redis..." ></Input>
                                    </FormItem>
                                    </Col>
                                    <Col span="3">
                                    <FormItem label=":" prop="connection_name"  >
                                         <InputNumber v-model="redis_port" style="width:100%" ></InputNumber>
                                    </FormItem>
                                    </Col>
                                  <Col span="3">
                                    <FormItem label=":" >
                                      <Input type="password" v-model="redis_pwd" placeholder="password" style="width:100%"  ></Input>
                                    </FormItem>
                                  </Col>
                                  <Col span="3">
                                    <FormItem label="DB:" >
                                      <InputNumber v-model="redis_db" style="width:100%" ></InputNumber>
                                    </FormItem>
                                  </Col>
                                  <div style="display: inline-block;float: right">
                                     <FormItem  label-position="center" style="margin-left: 10px">
                                        <Button  type="warning" icon="ios-redo"  @click.native="Test" ></Button>
                                       <Tooltip content="monitorkeytop">
                                              <Button  type="info"    icon="ios-redo"   :disabled="this.commit_val2" @click.native="Hotkey" >Redis</Button>
                                        </Tooltip>
                                        <Button  type="success" icon="ios-redo"   :disabled="this.commit_val" @click.native="Submmit" ></Button>
                                      </FormItem>
                                  </div>

                    </Row>
              </Form>
        </Card>
      <Card>
        <editor v-model="redis_text" @init="editorInit"></editor>
      </Card>
      <Card>
                      <Tabs type="card" closable  v-model="select_tab"  @on-click="MyFavite">
                  <TabPane label="" :key="3">
                      <RadioGroup v-model="help.type" @on-change="K_type" name="default">
                          <Radio label="Key"></Radio>
                          <Radio label="String"></Radio>
                          <Radio label="Hash"></Radio>
                          <Radio label="Set"></Radio>
                          <Radio label="List"></Radio>
                      </RadioGroup>
                      <br>
                      <br>
                    <Card>
                      <p style="white-space: pre-line;">{{this.help.text}}</p>
                    </Card>

                  </TabPane>
                  <TabPane label="" :key="4" :closable="false" name="res">
                    <Card style="background-color: #000000;color: #ffffff">
                    <p style="white-space: pre-line;" v-for="i in this.res">{{i}}</p>
                    </Card>
                  </TabPane>
              </Tabs>

      </card>
    </div>
</template>

<script>
  import ICol from '../../../node_modules/iview/src/components/grid/col.vue'
  import axios from 'axios'
  import util from '../../libs/util'

  export default {
    components: {
      ICol,
      editor: require('../../libs/editor')
    },
    name: 'SlowLog',
    data () {
      return {
        commit_val: true,
        commit_val2: true,
        select_tab: 'default',
        help: {
          text: '1exists keykeykey10\n' +
            '                                    2keys pattern keykey\n' +
            '                                    3expire key seconds keykey\n' +
            '                                    4ttl key keytime to livekey-2key-1\n' +
            '                                    5pttl key ttlkey\n' +
            '                                    6move key db key\n' +
            '                                    7type key keynonestringlistsetzsethash)\n' +
            '                                    8SCAN cursor [MATCH pattern] [COUNT count] The default COUNT value is 10',
          type: 'Key'
        },
        res: null,
        redis_text: '',
        redis_host: '',
        redis_port: '6379',
        redis_pwd: '',
        redis_db: '0'
      }
    },
    methods: {
      K_type () {
        if (this.help.type === 'String') {
          this.help.text = '1get key keykeynilkey\n' +
            '                                     2mget key [key...] keykeynilkeynil\n' +
            '                                     3strlen key keykey0key\n' +
            '                                     4append key value keykey\n' +
            '                                     5set key value [ex seconds] [px milliseconds] [nx|xx] key,expxnxkeyxxkey\n' +
            '                                     6setex key seconds value key\n' +
            '                                     7setnx key value keykey\n' +
            '                                     8mset key value [key value...] key\n' +
            '                                     9msetnx key value [key value...] msetmsetnxkey'
        }
        if (this.help.type === 'Key') {
          this.help.text = '1exists keykeykey10\n' +
            '                                    2keys pattern keykey\n' +
            '                                    3expire key seconds keykey\n' +
            '                                    4ttl key keytime to livekey-2key-1\n' +
            '                                    5pttl key ttlkey\n' +
            '                                    6move key db key\n' +
            '                                    7type key keynonestringlistsetzsethash)\n' +
            '                                    8SCAN cursor [MATCH pattern] [COUNT count] The default COUNT value is 10'
        }
        if (this.help.type === 'Hash') {
          this.help.text = '1hgetall key hashfield\n' +
            '                                     2hexists key field hashfield1keyfield0\n' +
            '                                     3hget key field hashfieldkeyfieldnil\n' +
            '                                     4hmget key field[field...] hashfieldkeyfieldnil\n' +
            '                                     5hkeys key hashfieldkey\n' +
            '                                     6hvals key hashvalkey\n' +
            '                                     7hdel key field[field...] hashfieldkeyfield\n' +
            '                                     8hset key field valuefield-valuehashkeyhashfield\n' +
            '                                     9hsetnx key field value hsethsetnxfield0'
        }
        if (this.help.type === 'Set') {
          this.help.text = '1smembers key key\n' +
            '                                     2scard key key\n' +
            '                                     3sdiff key[key...] key1\n' +
            '                                     4sunion key[key...] key\n' +
            '                                     5spop key keyNULL\n' +
            '                                     6sismember key member memberkey01\n' +
            '                                     7sadd key member[member...] memberkeymember\n' +
            '                                     8smove source destination membermembersourcedestinationmemberdestinationsourcesourcemember0destination\n' +
            '                                     9srem key member[member]keymembermember'
        }
        if (this.help.type === 'List') {
          this.help.text = '1BLPOP key1 [key2 ] timeout  \n' +
            '2BRPOP key1 [key2 ] timeout  \n' +
            '3BRPOPLPUSH source destination timeout  \n' +
            '4LINDEX key index \n' +
            '5LINSERT key BEFORE|AFTER pivot value \n' +
            '6LLEN key \n' +
            '7LPOP key \n' +
            '8LPUSH key value1 [value2] \n' +
            '9LPUSHX key value \n' +
            '10LRANGE key start stop \n' +
            '11LREM key count value \n' +
            '12LSET key index value \n' +
            '13LTRIM key start stop (trim)\n' +
            '14RPOP key \n' +
            '15RPOPLPUSH source destination \n' +
            '16RPUSH key value1 [value2] \n' +
            '17RPUSHX key value '
        }
      },
      Test () {
        axios.post(`${util.url}/redis`, {
          'redis_host': this.redis_host,
          'redis_port': this.redis_port,
          'redis_pwd': this.redis_pwd,
          'redis_db': this.redis_db,
          'redis_text': 'test'
          })
            .then(res => {
              if (res.data['error']) {
              this.$Message.error(res.data['error'])
              util.err_notice(res.data['error'])
            }
              if (res.data['ok']) {
              this.$Message.info(res.data['ok'])
              util.notice(res.data['ok'])
              this.commit_val = false
              this.commit_val2 = false
            }
            })
            .catch(() => {
              util.err_notice('Redis ')
            })
      },
      Hotkey () {
        this.commit_val2 = true
        this.select_tab = 'res'
        this.res = ''
        axios.post(`${util.url}/redis_faina`, {
          'redis_host': this.redis_host,
          'redis_port': this.redis_port,
          'redis_pwd': this.redis_pwd
          })
            .then(res => {
              if (res.data['error']) {
                this.commit_val2 = false
                this.$Message.error(res.data['error'])
                util.err_notice(res.data['error'])
                return
              }
              // this.res = String(res.data['data']).replace('"', '').replace(/,/g, '\r\n')
              this.res = res.data['data']
              this.commit_val2 = false
            })
            .catch(() => {
              this.commit_val2 = false
              util.err_notice('Redis_faina ')
            })
      },
      Submmit () {
        if (this.redis_text === '') {
          util.notice('')
          return
        }
        this.select_tab = 'res'
        this.res = ''
        axios.post(`${util.url}/redis`, {
          'redis_host': this.redis_host,
          'redis_port': this.redis_port,
          'redis_pwd': this.redis_pwd,
          'redis_db': this.redis_db,
          'redis_text': this.redis_text
          })
            .then(res => {
              if (res.data['error']) {
                this.$Message.error(res.data['error'])
                util.err_notice(res.data['error'])
                return
              }
              // this.res = String(res.data['data']).replace('"', '').replace(/,/g, '\r\n')
              this.res = res.data['data']
            })
            .catch(() => {
              util.err_notice('Redis ')
            })
      }
    },
    watch: {
    'redis_text' (newVal, oldVal) {
            localStorage.setItem('redis_text', newVal)
        },
    'redis_host' (newVal, oldVal) {
            localStorage.setItem('redis_host', newVal)
        },
    'redis_port' (newVal, oldVal) {
            localStorage.setItem('redis_port', newVal)
        },
    'redis_pwd' (newVal, oldVal) {
            localStorage.setItem('redis_pwd', newVal)
        },
    'redis_db' (newVal, oldVal) {
            localStorage.setItem('redis_db', newVal)
        }
  },
    mounted () {
       axios.put(`${util.url}/workorder/events`, {'permissions_type': 'query'})
        .then()
        .catch(error => {
          this.$Message.error('')
          util.err_notice(error)
        })
      if (localStorage.getItem('redis_text') !== null) {
            this.redis_text = localStorage.getItem('redis_text')
          }
      if (localStorage.getItem('redis_host') !== null) {
            this.redis_host = localStorage.getItem('redis_host')
          }
      if (localStorage.getItem('redis_port') !== null) {
            this.redis_port = localStorage.getItem('redis_port')
          }
      if (localStorage.getItem('redis_pwd') !== null) {
            this.redis_pwd = localStorage.getItem('redis_pwd')
          }
      if (localStorage.getItem('redis_db') !== null) {
            this.redis_db = localStorage.getItem('redis_db')
          }
    }
  }
</script>



